import style from "./styledProducts.module.css";
import React, { useState, useEffect } from "react";
import { useProducts } from "../../../../context/useProducts";
import categories from "../../../../data/categories.js";
import ProductCard from "../Card/ProductCard";
import Loader from "../../../../components/Loader/Loader";
import Pagination from "../../../../components/Pagination/Pagination";

const ProductsSect = () => {
  const { 
    list, 
    search, 
    category, 
    setSearch, 
    setCategory, 
    loading, 
    error, 
    pagination = { page: 1, total: 0, totalPages: 1, limit: 12 },
    fetchProducts,
    size,
    setSize,
    availableSizes
  } = useProducts();

  const [sortType, setSortType] = useState("newest");
  const [availability, setAvailability] = useState("all");
  const currentProducts = list || [];

  useEffect(() => {
    fetchProducts(1, 12);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }, [category, search]);

  const handlePageChange = (newPage) => {
    fetchProducts(newPage, 12);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const filterCategory = (category = "") => {
    setSearch("");
    setCategory(category);
  };
  
  const getFinalPrice = (product) => {
    if (product.discount) {
      return product.price * (1 - product.discount / 100);
    }
    return product.price;
  };

  const sortProducts = (products) => {
    if (sortType === "asc") {
      return [...products].sort((a, b) => getFinalPrice(a) - getFinalPrice(b));
    }
    if (sortType === "desc") {
      return [...products].sort((a, b) => getFinalPrice(b) - getFinalPrice(a));
    }
    if (sortType === "popularity") {
      return [...products].sort(
        (a, b) => (b.popularity || 0) - (a.popularity || 0)
      );
    }
    if (sortType === "discounts") {
      return [...products].sort((a, b) => {
        const aHasDiscount = !!a.discount;
        const bHasDiscount = !!b.discount;
        if (aHasDiscount && !bHasDiscount) return -1;
        if (!aHasDiscount && bHasDiscount) return 1;
        return getFinalPrice(a) - getFinalPrice(b);
      });
    }
    return products;
  };

  const availabilityFiltered = currentProducts.filter((p) => {
    if (availability === "inStock") return Number(p.stock) > 0;
    if (availability === "backorder") return Number(p.stock) === 0;
    return true;
  });

  const sortedProducts = sortProducts(availabilityFiltered);

  if (loading && list.length === 0) {
    return (
      <section id={style.prodsSection}>
        <Loader />
      </section>
    );
  }

  return (
    <section id={style.prodsSection}>
  <h1>
    {category === "todos" ? "Productos Populares" : `Productos ${category}`}
  </h1>

  <div className={style.layout}>
    <aside className={style.filters}>
      <label>
        Ordenar:
        <select
          value={sortType}
          onChange={(e) => setSortType(e.target.value)}
          className={style.selectInput}
        >
          <option value="">Populares</option>
          <option value="asc">Precio: Menor a mayor</option>
          <option value="desc">Precio: Mayor a menor</option>
          <option value="discounts">Descuentos</option>
        </select>
      </label>

      <label>
        Disponibilidad:
        <select
          value={availability}
          onChange={(e) => setAvailability(e.target.value)}
          className={style.selectInput}
        >
          <option value="all">Todos los productos</option>
          <option value="inStock">En stock</option>
          <option value="backorder">Sin stock</option>
        </select>
      </label>

      <label>
        Marca:
        <select
          value={category}
          onChange={(e) => {
            filterCategory(e.target.value);
          }}
          className={style.selectInput}
        >
          <option value="todos">Todos</option>
          {categories.map((cat) => (
            <option key={cat.name} value={cat.name}>
              {cat.name.charAt(0).toUpperCase() + cat.name.slice(1)}
            </option>
          ))}
        </select>
      </label>

      {/* Nuevo filtro por talle */}
      <label>
        Talle:
        <select
          value={size}
          onChange={(e) => setSize(e.target.value)}
          className={style.selectInput}
        >
          <option value="">Todos</option>
          {availableSizes.map((s) => (
            <option key={s} value={s}>
              {s}
            </option>
          ))}
        </select>
      </label>
    </aside>
    <div className={style.productsArea}>
      {loading ? (
        <div className={style.loaderContainer}>
          <Loader />
        </div>
      ) : sortedProducts.length > 0 ? (
        <>
          <div className={style.productsGrid}>
            {sortedProducts.map((prod) => (
              <ProductCard key={prod._id} product={prod} />
            ))}
          </div>
          {pagination && pagination.totalPages > 1 && (
            <div className={style.paginationContainer}>
              <Pagination
                currentPage={pagination.page}
                totalPages={pagination.totalPages}
                totalProducts={pagination.total}
                productsPerPage={pagination.limit}
                paginate={handlePageChange}
              />
            </div>
          )}
        </>
      ) : (
        <div className={style.noResults}>
          <h3>No se encontraron productos</h3>
          <p>Intenta con otros términos de búsqueda o categorías</p>
        </div>
      )}
    </div>
  </div>
</section>

  );
};

export default ProductsSect;
